import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Divider, Col, Checkbox} from "antd";

const { Option } = Select;
const InputGroup = Input.Group;

class AdjustmentTypeCreateForm extends Component {
  state = {
    check_code:false
  }
  
  handleChangeCode = () => {
    const {setFieldsValue} = this.props.form
    this.setState({check_code:!this.state.check_code})
    if(this.state.check_code===false){
      setFieldsValue({
        ["adjustment_type_code"]: null,
      })
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { adjustmentType } = this.props;
    const { loading } = adjustmentType;
    const error = adjustmentType.errorField ? adjustmentType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "50%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="adjustmentTypes.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )} */}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="adjustmentTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup compact>
              <Col span={20}>  
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="adjustmentTypes.add.code" />}
                validateStatus={
                  error.adjustment_type_code || getFieldError("adjustment_type_code")
                    ? "error"
                    : null
                }
                help={
                  error.adjustment_type_code
                    ? error.adjustment_type_code
                    : getFieldError("adjustment_type_code")
                }
              >
                {getFieldDecorator("adjustment_type_code", {
                  //initialValue: data.adjustment_type_code,
                  rules: [
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(
                  <Input
                    disabled={this.state.check_code ? true : false}
                  />
                )}
              </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{  
                      paddingTop:"5px",
                      paddingLeft: "8px",
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                  
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="label.description" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="adjustmentTypes.add.costAdjustment" />}
            >
              {getFieldDecorator("cost_adjustment", {
                initialValue: 0,
              })(
                <Radio.Group 
                  buttonStyle="solid"
                  style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="adjustmentTypes.add.itemTransfer" />}
            >
              {getFieldDecorator("item_transfer", {
                initialValue: 0,
              })(
                <Radio.Group
                 buttonStyle="solid"
                 style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
            
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="adjustmentTypes.add.subtitle.accounts"/>
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="adjustmentTypes.add.adjustmentAccount"  />}
            >
              {getFieldDecorator("id_account_adjustment", {
                //initialValue: data.id_account_adjustment,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={adjustmentType.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.adjustmentType.accounts.map((data, key) => (
                    // data.id_account_type === "13" ||
                    // data.id_account_type === "15" ||
                    // data.id_account_type === "16" ||
                    // data.id_account_type === "17" ||
                    // data.id_account_type === "18"
                    // ? 
                    (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    // : null
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>
        
            

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedAdjustmentTypeCreateForm = Form.create({ name: "create_adjustment_type" })(AdjustmentTypeCreateForm);

export default WrappedAdjustmentTypeCreateForm;
