import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import AdjustmentTypeCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/adjustmentType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/adjustment-types"

class CreateAdjustmentType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createAdjustmentType } = this.props;
    createAdjustmentType();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.adjustmentType.error) {
      notification("error", this.props.adjustmentType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.adjustmentType.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeAdjustmentType } = this.props;

    // Uppercasing adjustmentType code
    // value.adjustment_type_code = value.adjustment_type_code.toUpperCase();
    // console.log(value);
    storeAdjustmentType(value);
  };

  render() {

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="adjustmentTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <AdjustmentTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            adjustmentType={this.props.adjustmentType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    adjustmentType: state.adjustmentType,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateAdjustmentType);
